export default {
  name: 'ComponentEmit',
  props: {
    title: String,
    titleModifiers: {
      default: () => ({})
    },
    age: Number
  },
  emits: {
    myClick: null,
    clickRequired: ({
      age
    }) => age > 10 && age < 20,
    'update:title': null,
    'update:age': null
  },
  methods: {
    titleEmit(event) {
      let value = event.target.value;

      if (this.titleModifiers.capitalize) {
        value = `${value.charAt(0).toUpperCase()}${value.substring(1)}`;
      }

      this.$emit('update:title', value);
    },

    onClick() {
      this.$emit('myClick', {
        info: 'myclick msg'
      });
    },

    clickRequired() {
      this.$emit('clickRequired', {
        info: 'clickRequired',
        age: 12
      });
    }

  }
};